<template>
  <div
    :class="[ 'slider', { 'slider--full': isFull } ]"
    tabindex="-1"
    @mouseenter="isHover = true"
    @mouseleave="isHover = false"
    @keyup.esc="isFull = false"
  >
    <div :class="[ 'slider__images', { 'slider__images--full': isFull } ]">
      <transition :name="oldActive < active ? 'slide-left' : 'slide-right'" mode="in-out">
        <img class="slider__image" :src="items[active]" alt="" :key="active">
      </transition>

      <!-- Предзагрузка следующего|предыдущего слайда -->
      <img :src="items[active + 1] || ''" alt="" v-show="false">
      <img :src="items[active - 1] || items[items.length - 1]" alt="" v-show="false">
    </div>

    <div :class="[ 'control', { 'control--hover': isHover } ]" v-if="items.length > 1" @click.self="isHover = !isHover">
      <span class="control__prev" @click="prev"><icon-prev class="slider__icon" /></span>
      <span class="control__next" @click="next"><icon-next class="slider__icon" /></span>

      <div class="control__items">
        <span
          :class="[ 'control__item', { 'control__item--active': active === i } ]"
          v-for="(item, i) in items"
          :key="i"
          @click="change(i)"
        ></span>
      </div>

      <div class="control__action control__play" @click="togglePlay">
        <component class="slider__icon" :is="isPause ? 'iconPlay' : 'iconPause'" />
      </div>

      <a class="control__action control__load" :href="items[active]" target="__black">
        <icon-download class="slider__icon" />
      </a>

      <div class="control__action control__full" @click="toggleFull">
        <component class="slider__icon" :is="isFull ? 'iconFullOut' : 'iconFullIn'" />
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'c-slider',

  components: {
    iconPrev: () => import('mdi-vue/ArrowLeft'),
    iconNext: () => import('mdi-vue/ArrowRight'),
    iconFullIn: () => import('mdi-vue/ArrowExpand'),
    iconFullOut: () => import('mdi-vue/ArrowCollapse'),
    iconDownload: () => import('mdi-vue/Download'),
    iconPlay: () => import('mdi-vue/Play'),
    iconPause: () => import('mdi-vue/Pause')
  },

  props: {
    items: {
      type: Array,
      require: true
    }
  },

  data: () => ({
    active: 0,
    oldActive: null,
    timer: null,
    delay: 5000,
    isHover: false,
    isFull: false,
    isPause: false
  }),

  created () {
    if (this.items.length > 1) {
      this.start()
    }
  },

  destroyed () {
    this.stop()
  },

  methods: {
    toggleFull () {
      this.isFull = !this.isFull
      this.isHover = false
    },
    togglePlay () {
      this.isPause = !this.isPause

      if (this.isPause) {
        this.stop()
      } else {
        this.start()
      }
    },
    prev () {
      this.oldActive = this.active

      if (this.active < 1) {
        this.active = this.items.length - 1
      } else {
        this.active--
      }

      this.start()
    },

    next () {
      this.oldActive = this.active

      if (this.active < this.items.length - 1) {
        this.active++
      } else {
        this.active = 0
      }

      this.start()
    },

    change (i) {
      if (~i && i < this.items.length) {
        this.oldActive = this.active
        this.active = i
        this.start()
      }
    },

    start () {
      if (!this.isPause) {
        this.stop()
        this.timer = setTimeout(this.tick, this.delay)
      }
    },
    stop () {
      clearTimeout(this.timer)
    },

    tick () {
      if (!this.isHover) {
        this.next()
      }

      this.start()
    }
  }
}
</script>

<style scoped>
  .slider {
    user-select: none;
    position: relative;
    padding-bottom: 56.25%;
    height: 0;
    width: 100%;
    overflow: hidden;
    outline: none;
    transition: height .2s;
  }
  .slider--full {
    position: fixed;
    top: 0;
    left: 0;
    height: 100%;
    padding: 0;
    z-index: 15;
  }
  .slider__images {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: #fff;
  }
  .slider--full .slider__images {
    background: #000;
  }
  .slider__image {
    display: block;
    object-fit: cover;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 1;
    transition: transform 1s;
  }
  .slider--full .slider__image {
    object-fit: contain;
  }
  .slider__icon {
    display: block;
    width: 20px;
  }
  .slider--full .slider__icon {
    width: 30px;
  }

  .control {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    z-index: 3;
    display: block;
    box-sizing: border-box;
    transition: background .2s;
  }
  .control--hover {
    background: rgba(0,0,0,.3);
  }
  .control__next,
  .control__prev {
    position: absolute;
    top: 50%;
    padding: 40px 15px;
    color: #fff;
    box-sizing: border-box;
    cursor: pointer;
    transition: transform .2s, color .2s;
  }
  .control__prev {
    left: 0;
    transform: translate(-100%, -50%);
  }
  .control__next {
    right: 0;
    transform: translate(100%, -50%);
  }
  .control--hover .control__prev,
  .control--hover .control__next {
    transform: translate(0, -50%);
  }

  .control__items {
    display: flex;
    position: absolute;
    bottom: 0;
    left: 50%;
    padding: 13px 0;
    box-sizing: border-box;
    transform: translate(-50%, 100%);
    transition: transform .2s;
  }
  .control--hover .control__items {
    transform: translate(-50%, 0);
  }
  .control__item {
    border-radius: 50%;
    width: 10px;
    height: 10px;
    border: 1.5px solid #fff;
    margin: 5px;
    cursor: pointer;
    transition: border-color .2s, background .2s;
  }
  .control__item--active {
    background: #fff;
  }

  .control__action {
    position: absolute;
    bottom: 0;
    padding: 15px;
    color: #fff;
    cursor: pointer;
    box-sizing: border-box;
    transform: translateY(100%);
    transition: transform .2s;
  }
  .control__play {
    left: 0;
  }
  .control__full {
    right: 0;
  }
  .control__load {
    right: 50px;
  }
  .control--hover .control__action {
    transform: translateY(0);
  }

  @media (max-width: 500px) {
    .slider--full .slider__icon {
      width: 20px;
    }
    .control__items {
      display: none;
    }
  }
</style>